<template>
	<view>
		<view v-if="!carInfo">
			<u-gap height="20"></u-gap>
			<view class="setting-wrap">
				<view class="setting-item">
					<u-field v-model="carNumber" label-width="100" label="车牌号" placeholder="请输入车牌号"></u-field>
				</view>
				<view class="setting-item">
					<u-field v-model="carModel" label-width="100" label="车型" placeholder="请选择车型"></u-field>
				</view>
				<view class="setting-item">
					<u-field v-model="vehicle" label-width="100" label="车架号" placeholder="请输入车架号"></u-field>
				</view>
			</view>
			<u-gap height="20"></u-gap>
			<view class="btn-wrap">
				<u-button type="primary" shape="circle" @click="saveCar">保存</u-button>
			</view>
			<u-toast ref="uToast" />
		</view>
		<view v-else>
			<view>
				<text>车牌号</text>
				<text>{{carInfo.carNumber}}</text>
			</view>
			<view>
				<text>车型</text>
				<text>{{carInfo.carModel}}</text>
			</view>
			<view>
				<text>车架号</text>
				<text>{{carInfo.vehicleFrame}}</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				carNumber: '',
				carModel: '',
				vehicle: '',
				carInfo: ''
			}
		},
		onLoad() {
			this.getCar()
		},
		methods: {
			getCar() {
				let _this = this
				this.$u.api.getCar().then(res => {
					_this.carInfo = res
				})
			},
			saveCar() {
				this.$u.api.saveCar({
					userId: uni.getStorageSync("userId"),
					carNumber: this.carNumber,
					carModel: this.carModel,
					vehicle: this.vehicle
				}).then(res => {
					this.$refs.uToast.show({
						title: '操作成功'
					})
				})
			}
		}
	}
</script>

<style lang="scss">
	.setting-wrap {
		background-color: #fff;
		padding: 0 40rpx 0 30rpx;

		.u-section__title__text {
			line-height: 3 !important;
		}

		.setting-item {}
	}

	.btn-wrap {
		position: fixed;
		width: 80%;
		left: 50%;
		bottom: 90rpx;
		transform: translateX(-50%);
	}
</style>
